.order-confirmation-container 
{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: #f4f1e9;
    padding: 20px;
    background-image: url('/public/beige-abstract-wallpaper-background-image.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  
  .order-confirmation-content 
  {
    text-align: center;
    
    background: #fefef6;
    padding: 40px;
    border-radius: 12px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    max-width: 500px;
    width: 100%;
  }
  
  .order-confirmation-content h1 
  {
    color: #333;
    margin-bottom: 20px;
    font-size: 28px;
  }
  
  .order-number-container 
  {
    background-color: #63948B;
    color: white;
    padding: 20px;
    border-radius: 8px;
    margin-bottom: 20px;
  }
  
  .order-number 
  {
    font-size: 48px;
    font-weight: bold;
  }
  
  .order-confirmation-content p 
  {
    color: #666;
    margin-bottom: 30px;
    font-size: 18px;
  }
  
  .back-to-home-btn 
  {
    background-color: #615d57;
    color: white;
    border: none;
    padding: 12px 25px;
    border-radius: 8px;
    font-size: 16px;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .back-to-home-btn:hover 
  {
    background-color: #4a4640;
    font-size: 1.28em;
  }