/* components/home.css */
:root {
  --primary-font: 'Inter', system-ui, -apple-system, sans-serif;
  --overlay-gradient: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0.8)
  );
  --transition-smooth: cubic-bezier(0.4, 0, 0.2, 1);
}

.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  background-image: 
    linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url('/public/pexels-karolina-grabowska-5993559.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  font-family: var(--primary-font);
  padding-top: 80px;
  overflow-x: hidden;
  will-change: background-position;
}

.tagline {
  margin: 60px 0;
  font-size: clamp(2rem, 6vw, 48px); 
  font-weight: 800;
  color: #ffffff;
  text-align: center;
  position: relative;
  z-index: 2;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  letter-spacing: -1px;
  opacity: 0;
  transform: translateY(20px);
  animation: fadeInUp 0.8s var(--transition-smooth) forwards;
}

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-auto-rows: 280px;
  gap: 30px;
  padding: 20px;
  position: relative;
  z-index: 2;
  perspective: 1000px;
  width: 100%;
  max-width: 1200px; 
}

.tile {
  background-color: transparent;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  transition: 
    transform 0.6s var(--transition-smooth),
    box-shadow 0.3s ease;
  overflow: hidden;
  position: relative;
  transform-style: preserve-3d;
  opacity: 0;
  animation: fadeIn 0.6s ease forwards;
  will-change: transform, opacity; 
}

/* Staggered animations with updated method */
.tile:nth-child(1) { animation-delay: 0.2s; }
.tile:nth-child(2) { animation-delay: 0.4s; }
.tile:nth-child(3) { animation-delay: 0.6s; }
.tile:nth-child(4) { animation-delay: 0.8s; }

@keyframes fadeIn {
  to {
    opacity: 1;
  }
}

.tile:hover {
  transform: 
    translateY(-10px)
    rotateX(5deg) 
    ;
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.3);
}

.tile-content {
  position: relative;
  width: 100%;
  height: 100%;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  transition: box-shadow 0.3s ease;
}

.tile-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 20px;
  transition: 
    transform 0.6s var(--transition-smooth),
    filter 0.3s ease;
  will-change: transform; /* Performance hint */
}

.tile:hover .tile-image {
  transform: scale(1.15);
  filter: brightness(0.9); /* Subtle darkening on hover */
}

.tile-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--overlay-gradient);
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity 0.4s ease;
  border-radius: 20px;
}

.tile:hover .tile-overlay {
  opacity: 1;
}

.tile-text {
  color: #ffffff;
  font-size: 2em;
  font-weight: 700;
  text-align: center;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
  transform: translateY(20px);
  transition: 
    transform 0.4s ease,
    color 0.3s ease;
  letter-spacing: 0.5px;
}

.tile:hover .tile-text {
  transform: translateY(0);
  color: #f0f0f0; /* Subtle color change on hover */
}

/* Responsive Adjustments */
@media screen and (max-width: 768px) {
  .home-container {
    padding-top: 60px;
    background-attachment: scroll; /* Better mobile performance */
  }

  .tagline {
    margin: 40px 20px;
    font-size: 2rem;
  }

  .grid {
    grid-template-columns: 1fr;
    grid-auto-rows: 250px;
    gap: 20px;
    padding: 10px;
  }
}

/* Prefers Reduced Motion */
@media (prefers-reduced-motion: reduce) {
  .tile, .tagline {
    animation: none;
    transition: none;
  }
  
  .tile-image {
    transform: none;
  }
}