body, html {
    margin: 0;
    padding: 0;
    height: 100%;
  }
  
  .bill-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: calc(100vh - 80px); /* Subtract navbar height */
    padding: 100px;
    background-image: url('/public/beige-abstract-wallpaper-background-image.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  
  .bill-details {
    background-color: #615d57;
    color: white;
    border-radius: 12px;
    padding: 25px 45px 50px;
    width: 100%;
    max-width: 500px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .bill-details h2 {
    text-align: center;
    margin-bottom: 40px;
    font-size: 24px;
  }
  
  .bill-header {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 15px;
    margin-bottom: 20px;
    border-bottom: 1px solid #8e8e8e;
    padding-bottom: 15px;
  }
  
  .bill-header-item {
    display: flex;
    flex-direction: column;
  }
  
  .bill-header-item span {
    font-size: 16px;
    text-align: right;
  }
  
  .bill-table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
  }
  
  .bill-table th, .bill-table td {
    
    padding: 10px 0;
    text-align: left;
  }
  
  .bill-table th {
    font-weight: bold;
  }
  
  .bill-total {
    display: flex;
    justify-content: space-between;
    font-weight: bold;
    font-size: 18px;
    margin-bottom: 20px;
    padding-top: 10px;
    border-top: 1px solid #8e8e8e;
  }
  
  .bill-payment-options {
    display: flex;
    justify-content: space-between;
    gap: 15px;
  }
  
  .pay-credits-btn, .pay-online-btn {
    flex-grow: 1;
    padding: 12px;
    border: none;
    border-radius: 8px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .pay-credits-btn {
    background-color: #63948B;
    color: white;
    transition: all 0.3s ease;
  }
  .pay-credits-btn:hover{
    background-color: #67b0a3;
    font-size: 1.28em;
  }
  
  .pay-online-btn {
    background-color: #4a7c59;
    color: white;
    transition: all 0.3s ease;
  }
  .pay-online-btn:hover{
    background-color: #549567;
    font-size: 1.28em;
  }

  
  .back-btn {
    margin-top: 20px;
    padding: 12px 25px;
    background-color: #615d57;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 16px;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  .back-btn:hover
  {
    background-color: #726c64;
    font-size:1.28em;
  }